import {
  Component,
  OnInit,
  ElementRef,
  ViewChild,
  AfterViewInit,
  AfterViewChecked,
  ViewChildren,
  QueryList,
} from '@angular/core'
import { PgQueryService } from '../../services/pg-services/pg-query.service'
import { CryptoManagerService } from '../../services/crypto-manager.service'
import * as FileSaver from 'file-saver'
import { AlertManagerService } from '../../services/window-managers/alert-manager.service'
import {
  EventTableComponent,
  eventTableStates,
} from '../../event-table/event-table.component'
import { ImportPoolService } from '../../services/import-pool.service'
import { Subscription, Observable } from 'rxjs'

export enum ieManagerStates {
  importMode,
  exportMode,
}

@Component({
  selector: 'app-import-export-manager',
  templateUrl: './import-export-manager.component.html',
  styleUrls: ['./import-export-manager.component.scss'],
})
export class ImportExportManagerComponent
  implements OnInit, AfterViewInit, AfterViewChecked {
  element
  iestate: ieManagerStates
  isWithPassword: boolean = false
  poolIsOpened = false
  encodingPassword: string = ''
  filetoUpload: File | undefined
  @ViewChildren('eventTable') eventTableComponent: QueryList<
    EventTableComponent
  >
  @ViewChild('eventTable') eventTable: EventTableComponent

  constructor(
    private el: ElementRef,
    public pq: PgQueryService,
    public cm: CryptoManagerService,
    public alert: AlertManagerService,
    public ip: ImportPoolService
  ) {
    this.element = el.nativeElement
  }

  ngAfterViewInit(): void {}

  ngAfterViewChecked() {}

  ngOnInit(): void {
    this.closeModal()
  }

  closeModal() {
    this.poolIsOpened = false
    this.element.style.display = 'none'
    this.isWithPassword = false
    this.encodingPassword = ''
    this.filetoUpload = undefined
  }

  isImportMode() {
    return this.iestate === ieManagerStates.importMode
  }

  isExportMode() {
    return this.iestate === ieManagerStates.exportMode
  }

  isWithPasswordChanged() {
    this.encodingPassword = ''
  }

  importObsHandler(obs$: Observable<any>, successMessage: string) {
    const sub = obs$.subscribe((resp) => {
      if (resp) {
        const { data, isSecured } = resp
        let b = new Blob([data], {
          type: 'octet/stream',
        })
        const currenTime = new Date()
        const fileName = `${currenTime.getDate()}.${
          currenTime.getMonth() + 1
        }.${currenTime.getFullYear()} ${currenTime.getHours()}-${currenTime.getMinutes()}-${currenTime.getSeconds()}`
        const extension = isSecured ? `.nbds` : `.nbd`
        this.alert.success(' ', successMessage)
        FileSaver.saveAs(b, fileName + extension)
        this.closeModal()
      }
      sub.unsubscribe()
    })
  }

  onImportAllClicked() {
    const obs$ = this.pq.importEvents({
      password: this.encodingPassword,
      onImport: 'ALL',
    })
    this.importObsHandler(
      obs$,
      '       .'
    )
  }

  onImportFromPoolClicked() {
    const obs$ = this.pq.importEvents({
      password: this.encodingPassword,
      onImport: this.ip.getPool().map((event) => event.id),
    })
    this.importObsHandler(
      obs$,
      '       .'
    )
  }

  handleFileInput(files: FileList) {
    this.filetoUpload = files[0]
    this.encodingPassword = ''
  }

  isSecuredFile() {
    if (!!this.filetoUpload) {
      let fileNameArray = this.filetoUpload.name.split('.')
      const extension = fileNameArray[fileNameArray.length - 1]
      return extension === 'nbds'
    } else return false
  }

  isFileUploaded() {
    return !!this.filetoUpload
  }

  onExportFileClicked() {
    let fileReader = new FileReader()
    fileReader.readAsText(this.filetoUpload)
    fileReader.onload = (e) => {
      let textFromFile: string = String(fileReader.result)
      let array: number[] = textFromFile
        .split(',')
        .map((val: string | number) => (val = +val))
      if (this.isSecuredFile()) {
        if (this.cm.isPasswordRight(array, undefined, this.encodingPassword)) {
          this.alert.success(
            ' ',
            '  .    .'
          )
          this.pq
            .exportEvents({ data: array, password: this.encodingPassword })
            .subscribe((res) => {
              this.closeModal()
              this.alert.success(
                ' ',
                '  .'
              )
              window.location.reload()
            })
        } else {
          this.alert.error(' ', '   ')
          console.log('  ')
        }
      } else {
        this.pq
          .exportEvents({ data: array, password: this.cm.getDefaultPassword() })
          .subscribe((res) => {
            this.closeModal()
            this.alert.success(
              ' ',
              '  .'
            )
            window.location.reload()
          })
      }
    }
  }

  fillEventTable() {
    this.eventTableComponent.changes.subscribe(
      (comp: QueryList<EventTableComponent>) => {
        if (comp.first) {
          comp.first.setTableState(eventTableStates.importPage)
          comp.first.setEventTable(this.ip.getPool())
        }
      }
    )
  }

  handlePoolVisibility(isOpen: boolean) {
    this.poolIsOpened = !this.poolIsOpened
    if (isOpen)
      setTimeout(() => {
        this.eventTable.setTableState(eventTableStates.importPage)
        this.eventTable.setEventTable(this.ip.getPool())
      }, 0)
  }
}
