import { Injectable } from '@angular/core'
import { ImportPoolComponent } from '../import-pool/import-pool.component'
import { IEvent } from '../dto/event.dto'
import { AlertManagerService } from './window-managers/alert-manager.service'
import { EventTableService } from './event-table.service'

@Injectable({
  providedIn: 'root',
})
export class ImportPoolService {
  component: ImportPoolComponent
  private pool: IEvent[] = []
  constructor(
    public alert: AlertManagerService,
    public em: EventTableService
  ) {}

  addToPool(events: IEvent[], isFromSearch: boolean = false) {
    events = events.filter(
      (event) => !this.pool.some((poolEvent) => poolEvent.id === event.id)
    )
    if (events.length > 0) {
      events.forEach((event) => (event.importPoolState = true))
      this.em.changeHighlightMainEventTable(events)

      if (isFromSearch) this.em.changeStateByComparsion(events)

      this.pool = this.pool.concat(...events)
      this.alert.success(
        '',
        `     ${events.length} `
      )
    } else
      this.alert.warning(
        '',
        `       `
      )
  }

  removeFromPool(event: IEvent) {
    const index = this.pool.findIndex((ev) => ev.id === event.id)
    this.pool[index].importPoolState = false
    this.pool.splice(index, 1)
    this.em.changeHighlightMainEventTable([event])
  }

  getPool() : IEvent[] {
    return this.pool
  }
}
