import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core'
import { IEvent, IEventHeaders, emptyEvent } from '../dto/event.dto'
import { stateFlag } from '../dto/flag.dto'
import { PgQueryService } from '../services/pg-services/pg-query.service'
import { CryptoManagerService } from '../services/crypto-manager.service'
import { EventManagerService } from '../services/window-managers/event-manager.service'
import { IPerson } from '../dto/person.dto'
import { EventTableService } from '../services/event-table.service'
import { ImportPoolService } from '../services/import-pool.service'

export enum eventTableStates {
  mainPage,
  searchPage,
  importPage,
}

@Component({
  selector: 'app-event-table',
  templateUrl: './event-table.component.html',
  styleUrls: ['./event-table.component.scss'],
})
export class EventTableComponent implements OnInit {
  @Input() searchNumber: number
  @Output() eventIsSelected: EventEmitter<boolean> = new EventEmitter<boolean>()
  @Output() eventTableIsEmpty: EventEmitter<boolean> = new EventEmitter<
    boolean
  >()

  eventTable: IEvent[] = []
  eventTableHeaders = IEventHeaders

  selectedEvent: IEvent = { ...emptyEvent, persons: [] }
  isEventSelected: boolean = false

  eventTableState: eventTableStates
  isEmptyResponce = false

  constructor(
    public pgQ: PgQueryService,
    public cm: CryptoManagerService,
    public em: EventManagerService,
    public et: EventTableService,
    public im: ImportPoolService
  ) {}

  ngOnInit(): void {
    this.eventTableState = eventTableStates.mainPage
  }

  getAllEvents() {
    const sub = this.pgQ.getAllEvents().subscribe((res: Uint8Array[]) => {
      let events: IEvent[] = this.cm.decode(res).rows
      this.eventTable = events.map((ev) => {
        ev['persons'] = []
        ev['state'] = stateFlag.isReaded
        ev['importPoolState'] = false
        return ev
      })
      sub.unsubscribe()
    })
  }

  onOpenEditEventMenu(event: IEvent, i: number) {
    this.em.openEditEM(event)
  }

  eventTableRowPainter(selectedRow: HTMLCollection) {
    //remove old selection
    Array.from(document.getElementsByClassName(`selected`)).forEach((node) => {
      node.className = node.className.substring(
        0,
        node.className.indexOf('selected') - 1
      )
    })
    //and add new
    Array.from(selectedRow).forEach((node) => {
      node.className += ' selected'
    })
  }

  onEventTableClicked(e: IEvent, index: number) {
    this.eventIsSelected.emit(true)
    this.et.setSelectedEvent(e)
    this.selectedEvent = e
    this.isEventSelected = true

    this.eventTableRowPainter(
      document.getElementsByClassName(
        `${index} cell ${this.eventTableState === 0 ? 'main' : 'search'}`
      )
    )
    const sub = this.pgQ
      .getPersonsOfEvent(e.id)
      .subscribe((res: Uint8Array[]) => {
        let persons: IPerson[] = this.cm.decode(res).rows

        persons = persons.map((per: IPerson) => {
          per.state = stateFlag.isReaded
          return per
        })
        this.selectedEvent.persons = e.persons = persons
        sub.unsubscribe()
      })
  }

  setEventTable(et: IEvent[]) {
    this.eventTable = [...et]
    if (et.length === 0) this.eventTableIsEmpty.emit(true)
    else this.eventTableIsEmpty.emit(false)
  }

  setEmptySearchFlag(flag: boolean) {
    this.isEmptyResponce = flag
  }

  getEmptySearchFlag() {
    return this.isEmptyResponce
  }

  importPageRowHandler(event: IEvent) {
    if (
      !!this.eventTableState &&
      this.eventTableState === eventTableStates.importPage
    ) {
      return event.importPoolState
    } else return true
  }

  isMainPage() {
    return this.eventTableState === eventTableStates.mainPage
  }

  isSearchPage() {
    return this.eventTableState === eventTableStates.searchPage
  }

  isMainPageHeaderHandler(keyOfHeader: string) {
    if (keyOfHeader === 'buttons') {
      if (!this.isMainPage() && !this.isSearchPage()) return false
    }
    return true
  }

  setTableState(state: eventTableStates) {
    this.eventTableState = state
  }

  importTablePainter(event: IEvent) {
    const index = this.eventTable.findIndex((e) => e.id === event.id)
    // const className = event.importPoolState ?'in' :'out'
    Array.from(
      document.getElementsByClassName(
        `${index} cell ${this.eventTableState === 0 ? 'main' : 'search'}`
      )
    ).forEach((node) => {
      node.className += ' '
      if (node.className.indexOf(' in ') !== -1)
        node.className = node.className.replace(' in ', ' out ')
      else if (node.className.indexOf(' out ') !== -1)
        node.className = node.className.replace(' out ', ' in ')
    })
  }

  importEventClicked(event: IEvent) {
    this.im.addToPool([event])
    // this.importTablePainter(event)
  }

  removeFromPoolClicked(event: IEvent) {
    this.im.removeFromPool(event)
    // this.importTablePainter(event)
  }
}
