import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core'
import { PgQueryService } from '../services/pg-services/pg-query.service'
import { IEvent, IEventHeaders, emptyEvent } from '../dto/event.dto'
import { IPerson } from '../dto/person.dto'
import { PgService } from '../services/pg-services/pg.service'
import { EventManagerService } from '../services/window-managers/event-manager.service'
import { stateFlag } from '../dto/flag.dto'
import { EventTableService } from '../services/event-table.service'
import { CryptoManagerService } from '../services/crypto-manager.service'
import {
  EventTableComponent,
  eventTableStates,
} from '../event-table/event-table.component'

@Component({
  selector: 'app-main-page',
  templateUrl: './main-page.component.html',
  styleUrls: ['./main-page.component.scss'],
})
export class MainPageComponent implements OnInit, AfterViewInit {
  searchNumber: null | number = null
  isSelectedEvent: boolean = false
  @ViewChild('eventTable') eventTable: EventTableComponent

  constructor(
    public pgQ: PgQueryService,
    public pg: PgService,
    public em: EventManagerService,
    public et: EventTableService,
    public cm: CryptoManagerService
  ) {}
  ngAfterViewInit(): void {
    this.eventTable.getAllEvents()
    this.eventTable.setTableState(eventTableStates.mainPage)
    this.et.setMainPageComponent(this.eventTable)
  }

  ngOnInit(): void {}

  isEventGotPersons() {
    return this.eventTable.selectedEvent.persons.length !== 0
  }

  isEventSelected() {
    let state = false
    if (!this.eventTable) return false
    else !!this.eventTable.selectedEvent.persons
  }

  handleSearch(num: number) {
    this.searchNumber = num
  }

  handleClear() {
    this.searchNumber = null
  }

  eventIsSelected(state: boolean) {
    this.isSelectedEvent = state
  }
}
