import {
  Component,
  ElementRef,
  OnInit,
  ViewChild,
  AfterViewInit,
} from '@angular/core'
import { emptyContact, IContact } from '../../dto/contact.dto'
import { emptyEvent, IEvent } from '../../dto/event.dto'
import { emptyPerson, IPerson } from '../../dto/person.dto'
import { emptyTelephone, ITelephone } from '../../dto/telephone.dto'
import { SearchManagerService } from '../../services/window-managers/search-manager.service'
import {
  EventTableComponent,
  eventTableStates,
} from '../../event-table/event-table.component'
import { stateFlag } from '../../dto/flag.dto'
import { CryptoManagerService } from '../../services/crypto-manager.service'
import { ImportPoolService } from '../../services/import-pool.service'
import { EventTableService } from '../../services/event-table.service'

@Component({
  selector: 'app-search-manager',
  templateUrl: './search-manager.component.html',
  styleUrls: ['./search-manager.component.scss'],
})
export class SearchManagerComponent implements OnInit, AfterViewInit {
  element: any
  eventForSearch: IEvent = { ...emptyEvent, persons: [] }
  @ViewChild('eventTable') eventTableComponent: EventTableComponent
  rowIsSelected: boolean = false
  tableIsEmpty: boolean = true

  constructor(
    private el: ElementRef,
    private sm: SearchManagerService,
    private cm: CryptoManagerService,
    public ip: ImportPoolService,
    public et: EventTableService
  ) {
    this.element = this.el.nativeElement
    this.closeModal()
  }
  ngAfterViewInit(): void {
    this.eventTableComponent.setTableState(eventTableStates.searchPage)
    this.et.setSearchPageComponent(this.eventTableComponent)
  }

  ngOnInit(): void {}

  closeModal() {
    this.clearEvent()
    this.element.style.display = 'none'
  }

  clearEvent() {
    this.rowIsSelected = false
    let contact: IContact = { ...emptyContact }
    let telephone: ITelephone = { ...emptyTelephone, contacts: [] }
    let person: IPerson = { ...emptyPerson, telephones: [] }
    this.eventForSearch = { ...emptyEvent, persons: [] }

    telephone.contacts.push(contact)
    person.telephones.push(telephone)
    this.eventForSearch.persons.push(person)
    if (this.eventTableComponent) {
      this.eventTableComponent.setEventTable([])
      this.eventTableComponent.setEmptySearchFlag(false)
    }
  }

  onSearchClicked() {
    const sub = this.sm.searchInBase(this.eventForSearch).subscribe((res) => {
      let events: IEvent[] = this.cm.decode(res).rows
      console.log(events)
      if (events['type'] === 'empty') {
        sub.unsubscribe()
        this.eventTableComponent.setEventTable([])
        return this.eventTableComponent.setEmptySearchFlag(true)
      } else {
        events = events.map((ev) => {
          ev['persons'] = []
          ev['state'] = stateFlag.isReaded
          return ev
        })
        this.et.clearComparsion()
        this.eventTableComponent.setEventTable(events)
        this.et.getComparsion()
        sub.unsubscribe()
      }
    })
  }

  eventTableRowSelected(state: boolean) {
    this.rowIsSelected = state
  }

  eventTableIsEmpty(state: boolean) {
    this.tableIsEmpty = state
  }

  addToPoolAll() {
    this.ip.addToPool(this.eventTableComponent.eventTable, true)
  }

  addToPoolSelected() {
    this.ip.addToPool([this.eventTableComponent.selectedEvent], true)
  }
}
