import { Injectable } from '@angular/core'
import { SearchManagerComponent } from '../../modalWindows/search-manager/search-manager.component'
import { IEvent } from '../../dto/event.dto'
import { HttpHeaders, HttpClient } from '@angular/common/http'
import { backendUrl } from '../../../backend.conf'
import { CryptoManagerService } from '../crypto-manager.service'
import { catchError, share } from 'rxjs/operators'

const httpOptions = new HttpHeaders()
@Injectable({
  providedIn: 'root',
})
export class SearchManagerService {
  component: SearchManagerComponent

  constructor(private http: HttpClient, public cm: CryptoManagerService) {}

  openSearch() {
    this.component.element.style.display = 'block'
  }

  searchInBase(searchData: IEvent) {
    return this.http
      .post<any>(
        `${backendUrl}/search`,
        { search: this.cm.encode(searchData) },
        { headers: httpOptions, responseType: 'json' }
      )
      .pipe(
        catchError(async (err) => console.log(err)),
        share()
      )
  }
}
