import { Injectable } from '@angular/core'
import { IEvent } from '../dto/event.dto'
import { PgQueryService } from './pg-services/pg-query.service'
import { EventTableComponent } from '../event-table/event-table.component'

@Injectable({
  providedIn: 'root',
})
export class EventTableService {
  private selectedEvent: IEvent
  private mainPageComponent: EventTableComponent
  private searchPageComponent: EventTableComponent
  private comparsionTable: { searchTable: number; mainTable: number }[]

  constructor(public pq: PgQueryService) {}

  onRemoveClicked() {
    return this.pq.setRemoveEvent(this.selectedEvent)
  }

  setSelectedEvent(event: IEvent) {
    this.selectedEvent = event
  }

  getSelectedEvent() {
    return this.selectedEvent
  }

  setMainPageComponent(component: EventTableComponent) {
    this.mainPageComponent = component
  }

  setSearchPageComponent(component: EventTableComponent) {
    this.searchPageComponent = component
  }

  changeHighlightMainEventTable(events: IEvent[]) {
    events.forEach((event) => {
      if (!!this.mainPageComponent)
        this.mainPageComponent.importTablePainter(event)
      if (!!this.searchPageComponent)
        this.searchPageComponent.importTablePainter(event)
    })
  }

  getComparsion() {
    let mainTable: IEvent[] = this.mainPageComponent.eventTable
    let searchTable: IEvent[] = this.searchPageComponent.eventTable

    searchTable.forEach((sE, index) => {
      this.comparsionTable.push({
        mainTable: mainTable.findIndex((mE) => mE.id === sE.id),
        searchTable: index,
      })
    })
  }

  clearComparsion() {
    this.comparsionTable = []
  }

  changeStateByComparsion(events: IEvent[]) {
    events.forEach((event, index) => {
      const actualState = event.importPoolState
      const mainTableIndex = this.comparsionTable[index].mainTable
      this.mainPageComponent.eventTable[
        mainTableIndex
      ].importPoolState = actualState
    })
  }
}
